import React, {Component} from 'react';
import axios from 'axios';
import { getUrl, getStatic } from './utilities';

var kk = {
    margin : "-10px"
        }
var lk = {
    height : "100%"
}

export default class Home extends React.Component {
    constructor(props){
        super(props);
        this.state={
            data:null,
        };  
    }

    getuserdata = () => {
        axios.get(getUrl('/data/user/get/'))
            .then((response)=> { 
                console.log(response.data['username']);
            })
            .catch(function(error){
                console.log(error);
            });
    }
  
    render() {
        return  (<div style={lk}><center> <img src={getStatic("/index.png")} width="30" height="30" style={kk}/> &nbsp;&nbsp;&nbsp; Instagram &nbsp; &nbsp; &nbsp;&nbsp;
        <input type="text" placeholder="search"/> &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
        <a><img src={getStatic("/images.png")} onClick={this.getuserdata} width="30" height="30" style={kk}/></a> &nbsp; &nbsp; &nbsp; &nbsp;</center>
        </div>
        );
    } 
}
